.cart-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: auto;
    padding: 10px;
    border-radius: 5px;
    background-color: #e3ebe7;
}

.actions {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.actions p {
    margin: 0;
}

.actions input {
    width: 15%;
}